.main {
	margin-left: 30px;
	margin-right: 30px;
	height: calc(100vh - 140px);
	overflow: scroll;
}


/* Page area styling */

.page {
	position: fixed;
	margin: auto;
	width: 100vw;
	background: white;
	padding-top: 10px;
}

.main h1 {
	margin-top: 20px;
	margin-bottom: 10px;
	font-size: 30px;
}

.main h1:first-child {
	margin-top: 15px;
}

.main h2 {
	margin-top: 10px;
	margin-bottom: 10px;
	font-size: 24px;
}

.main h2 a {	
	text-decoration: none;
	color: inherit;
}

.main h3 {
	margin-top: 10px;
	margin-bottom: 10px;
	font-size: 18px;
	font-style: italic;
}

.main p {
	margin-bottom: 10px;
}

.main ul, .main ol {
	margin-left: 30px;
	margin-bottom: 10px;
}

.main img {
	display: block;
	max-width: calc(100vw * 0.6180340 / 2);
	margin: 20px auto;
}

.main hr {
	border: 1px solid #002147;
}

.main table {
	margin-bottom: 10px;
	width: 100%;
}

.main td {
	background: #e8e8e8;
	padding: 3px;
	text-align: center;
}

.main th {
	background: #dddddd;
	padding: 5px;
	text-align: center;
}